import React from 'react'
import {Forest,Tree} from '../../data/Imagedata'

import './VerifyNFT.scss'

const VerifyNFT = () => {
  return (
    <main>
        {/* NFT*/}
  <section className='VerifyNft' style={{ backgroundImage: `url(${Forest})`,backgroundRepeat:"no-repeat",backgroundSize:"cover"}} > 

    <img classname='heroimagenft' src={Tree} alt="RTS tREE" />
  </section>
    </main>
  )
}

export default VerifyNFT